import React from "react";
import { useRef } from "react";
import "../styles/components/SideMenu.scss";

import resumeArray from "../assets/resume";

export default function SideMenu() {
  const mainDiv = useRef();
  const mainButton = useRef();

  const documentBottom = document.body.scrollHeight;

  const onMenuButtonClick = () => {
    mainDiv.current.classList.toggle("side-menu-main-div-hidden");
    mainButton.current.classList.toggle("is-active");
  };

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  const scrollToBottom = () => {
    window.scrollTo({ top: documentBottom, behavior: "smooth" });
  };

  const scrollToResumeSection = (suffix) => () => {
    document.getElementById(`resume${suffix}`).scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };

  const renderItem = (liClassName, onClick, text, key = "") => {
    return (
      <li key={key} id={key} className={liClassName}>
        <button onClick={onClick} className="side-menu-item-button">
          {text}
        </button>
      </li>
    );
  };

  const renderResumeItems = () => {
    return (
      <>
        {resumeArray &&
          resumeArray.map((resumeSection) => {
            return (
              <div key={`side-menu-item-resume-div-${resumeSection[0]}`}>
                {renderItem(
                  "side-menu-item-2",
                  scrollToResumeSection(`-${resumeSection[0]}`),
                  resumeSection[1],
                  `side-menu-item-resume-${resumeSection[0]}}`
                )}
                {resumeSection[2] &&
                  resumeSection[2].map((resumeSubsection) => {
                    if (resumeSubsection[1] && resumeSubsection[1] !== "") {
                      return renderItem(
                        "side-menu-item-3",
                        scrollToResumeSection(
                          `-${resumeSection[0]}-${resumeSubsection[0]}`
                        ),
                        resumeSubsection[1],
                        `side-menu-item-resume-${resumeSection[0]}-${resumeSubsection[0]}`
                      );
                    } else {
                      return null;
                    }
                  })}
              </div>
            );
          })}
      </>
    );
  };

  return (
    <>
      <button onClick={onMenuButtonClick} className="side-menu-button">
        <div ref={mainButton} className="side-menu-button-hamburger">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </button>
      <div
        ref={mainDiv}
        className="side-menu-main-div side-menu-main-div-hidden"
      >
        <h1>Min-Kyu Lee</h1>
        <ul className="side-menu-items">
          {renderItem(
            "side-menu-item",
            scrollToTop,
            "Top of Page",
            "side-menu-item-resume-top"
          )}
          {renderResumeItems()}
          {renderItem(
            "side-menu-item",
            scrollToBottom,
            "Bottom of Page",
            "side-menu-item-resume-bottom"
          )}
        </ul>
      </div>
    </>
  );
}
